import React, { useState } from 'react';
import ReactWordcloud from 'react-wordcloud';
import { S } from './style';
// import { words } from './words';
import { deduplicateArray, removeOneWords } from '../../util/words';
import { getWords } from '../../api';
import { useEffect } from 'react';

const options = {
  colors: ['#1f77b4', '#ff7f0e', '#2ca02c', '#d62728', '#9467bd', '#8c564b'],
  enableTooltip: true,
  deterministic: false,
  fontFamily: 'sans-serif',
  fontSizes: [10, 100],
  padding: 1,
  rotations: 3,
  rotationAngles: [0, 90],
  scale: 'sqrt',
  spiral: 'archimedean',
  transitionDuration: 500,
};

function Thermometer() {
  const [words, setWords] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const response = await getWords(localStorage.getItem('userId'));
      setWords(removeOneWords(deduplicateArray(response.data.data)));
    };
    fetchData();
  }, []);

  return (
    <S.AppHeader>
      <S.h1>Ïò®ÎèÑÍ≥Ñ</S.h1>
      <S.Information>
        <S.Analyze>
          2021ÎÖÑ Ïù¥ÏõîÏùò Í≤åÏãúÍ∏Ä Ïò®ÎèÑÎäî <S.Temperature>64&deg;C</S.Temperature> ÏûÖÎãàÎã§.
        </S.Analyze>
        <S.Result>Ï¥ù 23Í∞úÏùò Í∏Ä Î∂ÑÏÑù Í≤∞Í≥º</S.Result>
      </S.Information>

      <S.Table>
        <table>
          <tbody>
            <S.Degree>
              <S.Warm>Ê∫´Ïò®</S.Warm>
              <S.Mid>‰∏≠Ï§ë</S.Mid>
              <S.Cold>Ô§ÆÎÉâ</S.Cold>
            </S.Degree>
            <tr>
              <td>
                <S.textarea cols="30" rows="10" readOnly></S.textarea>
              </td>
              <S.MidTextArea>
                <S.textarea cols="30" rows="10" readOnly></S.textarea>
              </S.MidTextArea>
              <td>
                <S.textarea cols="30" rows="10" readOnly></S.textarea>
              </td>
            </tr>
          </tbody>
        </table>
      </S.Table>

      <div style={{ height: 600 }}>
        <S.h1>ÎÇòÏùò Îã®Ïñ¥</S.h1>
        <ReactWordcloud options={options} words={words} />
      </div>
    </S.AppHeader>
  );
}

export default Thermometer;
